@import url("https://fonts.googleapis.com/css2?family=Anonymous+Pro:wght@400;700&family=Courier+Prime:wght@400;700&family=Inconsolata&family=Rationale&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Anonymous+Pro:wght@400;700&family=Courier+Prime:wght@400;700&family=Inconsolata&family=Rationale&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Anonymous+Pro:wght@400;700&family=Courier+Prime:wght@400;700&family=Inconsolata&family=Rationale&display=swap");
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Rationale", Helvetica, sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Rationale", Helvetica, sans-serif;
  margin: 2.5rem;
  font-weight: 400;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.header {
  background-color: rgba(0, 0, 0, 0.9);
  padding: 1rem 0 5rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}

.footer {
  background-image: url(../img/footer.jpg);
  font-family: "Courier", Helvetica, sans-serif;
}

.footer-container-1 {
  background-color: rgba(0, 0, 0, 0.9);
  padding: 5rem 3rem;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 3rem;
}
.footer-container-1 h1, .footer-container-1 h2 {
  color: #FF6F0F;
  margin: 0 0 3rem 0;
}
.footer-container-1 h2 {
  font-size: 2.5rem;
}
.footer-container-1 p, .footer-container-1 a {
  color: #FFFFFF;
  margin: 0;
  text-align: justify;
}
.footer-container-1 .footer-information {
  padding: 0 2rem;
}
.footer-container-1 .footer-information .social {
  display: flex;
  margin: 2rem 0;
  gap: 1rem;
  justify-content: center;
}
.footer-container-1 .footer-information .social a {
  line-height: 0;
}
.footer-container-1 .footer-information .social .social-btn {
  display: flex;
  border: 2px solid #a0a0a0;
  border-radius: 10%;
  justify-content: center;
  align-items: center;
}
.footer-container-1 .footer-information .social .social-btn:hover {
  cursor: pointer;
}
.footer-container-1 .footer-information .social .social-btn svg {
  padding: 0.3rem;
  stroke: #a0a0a0;
}
.footer-container-1 .footer-information .footer-address {
  display: flex;
  gap: 2rem;
  padding-bottom: 1rem;
}
.footer-container-1 .footer-information .footer-address svg {
  stroke: #a0a0a0;
  width: 30px;
  height: 30px;
}
.footer-container-1 .footer-information .footer-links {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.footer-container-1 .footer-information .footer-links a {
  font-size: 2rem;
}
.footer-container-1 .footer-information .footer-links a:hover {
  cursor: pointer;
}

.footer-container-2 {
  background-color: #000000;
  display: flex;
  justify-content: space-between;
  padding: 0 3rem;
}
.footer-container-2 p, .footer-container-2 a {
  color: #FFFFFF;
  font-family: "Rationale", Helvetica, sans-serif;
}